import rollbarLoader from '../lib/rollbar/rollbar_loader.js';
const rollbar = rollbarLoader();
window.rollbar = rollbar;

import appSettings from '../settings.js';
import UrlInspector from '../lib/url_inspector.js';
import defaultBlockedDomains from '../lib/blocked_domains.js';

import {callbackErrorHandler, checkNested} from '../lib/util.js';

const urlParser = require('url');

var browser;

if (!browser) {
    browser = chrome;
}

// always allow browser extension domain and freedom domain
let browserRuntimeId;
try{
    browserRuntimeId = browser.runtime.id;
} catch (e) {
    browserRuntimeId = null;
}

var allowedDomains = [
    "ljfdccdjpfjpfjbpdiihanpodilolofh",
    "freedom.to"
];

allowedDomains.push(browserRuntimeId);

var inspector;
var pausePageCounter = 0;

var setExtensionInactive = function () {
    browser.browserAction.setIcon({path: 'images/pause_icon_off_48.png'});
    inspector.setInactive();
    browser.storage.sync.set({'extensionIsActive': false});
    browser.browserAction.setTitle({'title':'Pause is disabled. Click to start pausing distracting sites'});

    browser.tabs.query({}, callbackErrorHandler(function(tabs) {
        tabs.forEach(function(tab) {
            if (tab){
                browser.tabs.sendMessage(tab.id, {'subject':'extensionInactive'});
            }
        });
    }));
};

var setExtensionActive = function (){
    browser.browserAction.setIcon({path: 'images/pause_icon_48.png'});
    inspector.setActive();
    browser.storage.sync.set({'extensionIsActive': true});
    browser.browserAction.setTitle({'title':"Pause is enabled, and you're pausing distracting sites"});

    // gets all the tabs in all the windows, and sends them a message to make the extension active
    browser.tabs.query({}, callbackErrorHandler(function(tabs) {
        tabs.forEach(function(tab) {
            if (tab){
                browser.tabs.sendMessage(tab.id, {'subject':'extensionActive'});
            }
        });
    }));
};

// get the allowed and blocked domains from storage and initialize the Url Inspector
// allowed domains need to be in storage in case the extension is turned off and on again
var initializeExtension = function() {
    return new Promise((resolve, reject) => {
        browser.storage.sync.get([
            'allowedDomainsUntil', 'extensionIsActive', 'blockedDomains',
        ], callbackErrorHandler(function(result){
            
            if (typeof result === 'undefined') {
                result = {};
            }
            
            let allowedDomainsUntil;
            let extensionIsActive = true;

            if(result && 'allowedDomainsUntil' in result) {
                allowedDomainsUntil = result.allowedDomainsUntil;
            }

            if(result && 'extensionIsActive' in result) {
                extensionIsActive = result.extensionIsActive;
            }

            // initialized blocked domains as default list (first install)
            // if they are present in storage, use the list from storage
            let blockedDomains = defaultBlockedDomains;
            
            if (result && 'blockedDomains' in result) {
                blockedDomains = result.blockedDomains;
            } else {
                browser.storage.sync.set({'blockedDomains': blockedDomains});
            }

            inspector = new UrlInspector(blockedDomains, allowedDomainsUntil, allowedDomains);
            
            // turn off extension, if isActive is false from storage
            if (!extensionIsActive){
                setExtensionInactive();
            }

            resolve();
        }));
    });
};

// syncs the storage with the blocked domains loaded in the inspector
var syncBlockedDomains = function() {
    browser.storage.sync.get(['blockedDomains'], callbackErrorHandler(function(result){
        if ('blockedDomains' in result) {
            inspector.setBlockedDomains(result.blockedDomains);
        }
    }));
};

// allowDomain function
// tells the inspector to allow the domain, and saves the domain in browser storage
// we need allowed domains to be in storage in case the extension is turned off and on again
var allowDomain = function(url, callback) {

    var allowUntilTimestamp = new Date().getTime() + appSettings.ALLOWED_DOMAIN_TIMEOUT;
    inspector.allowDomainUntil(url, allowUntilTimestamp);

    browser.storage.sync.get(['allowedDomainsUntil'], callbackErrorHandler( function(result){
        
        let hostname = inspector.getHostname(url);

        let allowedDomainsUntil = {};
        if('allowedDomainsUntil' in result) {
            allowedDomainsUntil = result.allowedDomainsUntil;
        }   
        
        allowedDomainsUntil[hostname] = allowUntilTimestamp;
        browser.storage.sync.set({'allowedDomainsUntil': allowedDomainsUntil}, callback);
    }));
};

// addBlockedDomain function
// tells the inspector to add a new blocked domain in the pause list, and adds it in storage
var addBlockedDomain = function(domain, callback) {

    if (inspector.isDomainAllowed(domain)) {
        callback({'error': 'Cannot add '+domain+' to the Pause list'});
        return;
    }

    let blockSubdomains = true;
    // for now facebook is hardcoded as the only domain where we would not block subdomains
    // in the future this will be an extra parameter
    if (domain == "facebook.com"){
        blockSubdomains = false;
    } 

    if (!inspector.addBlockedDomain(domain, null, blockSubdomains)) {
        callback({'error': 'Error adding website to pause list. Invalid domain: '+domain});
        return;
    }

    browser.storage.sync.get(['blockedDomains'], callbackErrorHandler(function(result){

        for (let blockedDomain of result.blockedDomains) {
            if (blockedDomain.domain == domain) {
                callback({'error': domain+' is already present in the pause list.'});
                return;
            }
        }

        result.blockedDomains.push({'domain': domain, 'blockSubdomains': blockSubdomains });
        browser.storage.sync.set(
            {'blockedDomains': result.blockedDomains}, 
            callbackErrorHandler(function(){
                callback({});
            })
        );
    }));
};

// removeBlockedDomain function
// tells the inspector to remove a blocked domian from the pause list and updates the storage
var removeBlockedDomain = function(domain, callback) {
    inspector.removeBlockedDomain(domain);

    browser.storage.sync.get(['blockedDomains'], callbackErrorHandler(function(result){
        let i = 0;
        for (let blockedDomain of result.blockedDomains) {
            if (blockedDomain.domain == domain) {
                result.blockedDomains.splice(i, 1);
                break;
            }
            i++;
        }
        browser.storage.sync.set(
            {'blockedDomains': result.blockedDomains}, 
            callbackErrorHandler(function(){
                callback();
            })
        );

    }));
};

// increment local in-memory page counter, and set the load date in storage
var incrementPausePageCounter = function(callback){
    pausePageCounter++;
    callback(pausePageCounter);

    browser.storage.sync.get(['pausePageOpens'], callbackErrorHandler(function(result){
        let pausePageOpens;
        if ('pausePageOpens' in result) {
            pausePageOpens = result.pausePageOpens;
            if (Array.isArray(pausePageOpens)) {
                pausePageOpens.push(new Date().toJSON());
            } else {
                pausePageOpens = [new Date().toJSON()];
            }
        } else {
            pausePageOpens = [new Date().toJSON()];
        }
        browser.storage.sync.set({'pausePageOpens': pausePageOpens});
    }));
};

var setPauseTimeout = function(timeout, callback) {
    if (isNaN(timeout)) {
        callback({'error':'Invalid Timeout value'+timeout});
    } else {
        if(timeout > 300 || timeout < 1) {
            callback({'error':'The timeout value must be between 1 and 300 seconds'});
            return;
        }

        browser.storage.sync.set({'pauseTimeout': parseInt(timeout)}, function(){
            callback({'success': true});
        });
    }
};

var getStats = function () {
    return new Promise ((resolve,reject) => {
        browser.storage.sync.get(['pausePageOpens'], callbackErrorHandler(function(result) {
            let pausePageOpens;
            if ('pausePageOpens' in result) {
                pausePageOpens = result.pausePageOpens;
                if (Array.isArray(pausePageOpens)) {
                    
                    let firstOpen = pausePageOpens[0];
                    let countOpens = pausePageOpens.length;

                    // a 24 element array where each hour will be hold the cumulative for that hour
                    let opensByHour = new Array(24).fill(0);
                    
                    for (let pausePageOpen of pausePageOpens) {
                        if (pausePageOpen < firstOpen) {
                            firstOpen = pausePageOpen;
                        }

                        let pausePageOpenDate = new Date(pausePageOpen);
                        opensByHour[ pausePageOpenDate.getHours()] += 1;
                    }

                    var dateDiff = function(date1, date2) {
                        let dt1 = new Date(date1);
                        let dt2 = new Date(date2);
                        return Math.floor(
                            (Date.UTC(dt2.getFullYear(), dt2.getMonth(), dt2.getDate()) - Date.UTC(dt1.getFullYear(), dt1.getMonth(), dt1.getDate()) ) /(1000 * 60 * 60 * 24)
                        );
                    };

                    let daysSinceFirstOpen = dateDiff(firstOpen, new Date());

                    let weeksSinceFirstOpen = daysSinceFirstOpen/7;
                    if ( weeksSinceFirstOpen < 1 ) {
                        weeksSinceFirstOpen = 1;
                    }

                    let monthsSinceFirstOpen = daysSinceFirstOpen/30.5;
                    if ( monthsSinceFirstOpen < 1 ) {
                        monthsSinceFirstOpen = 1;
                    }

                    let opensPerDay = Math.round(100 * countOpens / daysSinceFirstOpen ) / 100;
                    let opensPerWeek = Math.round(100 * countOpens / weeksSinceFirstOpen) / 100;
                    let opensPerMonth = Math.round(100 * countOpens / monthsSinceFirstOpen) / 100;

                    resolve({
                        'opensPerDay': opensPerDay,
                        'opensPerWeek': opensPerWeek,
                        'opensPerMonth': opensPerMonth,
                        'opensByHour': opensByHour
                    });

                } else {
                    reject();
                }
            } else {
                reject();
            }
        }));
    });
};

var resolveMessage = function(msg, sender, msgCallback) {
    if ( msg.subject === 'allowDomainOnce' ) {
        // allow domain once, for specific tab (continue button)
        let tabId = null;

        // check  if the sender.tab.id is present
        // messages from popups will not have a sender.tab.id
        if (checkNested(sender, 'tab', 'id')) {
            tabId = sender.tab.id;
        } 

        inspector.allowDomainOnce(msg.url, tabId);
        msgCallback();

    } else if (msg.subject === 'allowDomain' ) {
        // allow domain for 24 hours (persistent)
        allowDomain(msg.url, msgCallback);
        return true;

    } else if (msg.subject === 'isUrlAllowed' ) {
        // check if url is allowed, for specific tab
        // wrap it in a try catch block, so that if there is a bug, always return true
        if (!msg.url || msg.url == ""){
            msgCallback(true);
        }

        if (!sender.tab) {
            msgCallback(true);    
        }

        let resultIsAllowed = true;
        try {
            let tabId = null;
            // make sure that sender.tab.id is present, otherwise we will be getting rollbar errors
            // messages from popups will not have a sender.tab.id
            if (checkNested(sender, 'tab', 'id')) {
                tabId = sender.tab.id;
            }

            resultIsAllowed = inspector.isUrlAllowed(msg.url, tabId, msg.referrer);
        } catch (e) {
            rollbar.error(e);
        }
        msgCallback(resultIsAllowed);

    } else if (msg.subject === 'getBlockedUrlName' ) {
        msgCallback(inspector.getBlockedUrlName(msg.url));

    } else if (msg.subject === 'toggleOnOff' ) {
        var toggle = inspector.getActiveState();

        toggle = !toggle;
        
        if(toggle){
            setExtensionActive();
        }
        else{
            setExtensionInactive();
        }
        msgCallback(inspector.getActiveState());

    } else if (msg.subject === 'getActiveState' ) {
        msgCallback(inspector.getActiveState());

    } else if (msg.subject === 'getBlockedDomains' ) {
        browser.storage.sync.get(['blockedDomains'],  callbackErrorHandler(function(result){
            msgCallback(result.blockedDomains);
        }));
        return true;
    } else if (msg.subject === 'getPauseTimeout' ) {
        browser.storage.sync.get(['pauseTimeout'],  callbackErrorHandler(function(result){
            if (!result.pauseTimeout){
                msgCallback(5);
            } else {
                msgCallback(result.pauseTimeout);
            }
            
        }));
        return true;

    } else if (msg.subject === 'addBlockedDomain' ) {
        addBlockedDomain(msg.domain, msgCallback);
        return true;

    } else if (msg.subject === 'removeBlockedDomain' ) {
        removeBlockedDomain(msg.domain, msgCallback);
        return true;
    } else if (msg.subject === 'incrementPausePageCounter' ) {
        incrementPausePageCounter(msgCallback);
        return true;
    } else if (msg.subject === 'setPauseTimeout' ) {
        setPauseTimeout(msg.timeout, msgCallback);
        return true;
    } else if (msg.subject === 'getStats' ) {
        getStats().then( (result) => {
            msgCallback(result);
        }, () => {
            msgCallback({'error': 'No stats found'});
        });
        return true;
    }
};

initializeExtension();

// sync the local blocked domains with the sync storage every 10 minutes
setInterval(function(){
    syncBlockedDomains();
}, 10 * 60 * 1000); // 10 minutes

// message listener
// listen from various messages from pages and content scripts
browser.runtime.onMessage.addListener( callbackErrorHandler( function (msg, sender, msgCallback) {
    if (!inspector) {
        // if inspector is not initialized, initialize it, and then resolve the message
        initializeExtension().then(function() {
            resolveMessage(msg, sender, msgCallback);
        });
        return true;
    } else {  
        return resolveMessage(msg, sender, msgCallback);
    }

    
}));

// listen for tab create
// this is to catch opening secondary pages in new tabs on pages that were allowed once (continue btn)
browser.tabs.onCreated.addListener(callbackErrorHandler(function(newTab){
    
    if (!newTab) {
        return;
    }


    // since chrome 79, there is a new parameter pendingUrl. 
    // depending on timing, we can find the URL in url or pendingUrl
    let newTabUrl = newTab.url;
    if (!newTabUrl) {
        newTabUrl = newTab.pendingUrl;
    }

    if (!inspector) {
        // if inspector is not initialized, initialize it, and then processNewTabOpen
        initializeExtension().then(function() {
            inspector.processNewTabOpen(newTab.openerTabId, newTab.id, newTabUrl);
        });
        return true;
    } else {
        inspector.processNewTabOpen(newTab.openerTabId, newTab.id, newTabUrl);
    }
    
}));

browser.runtime.setUninstallURL("https://freedom-app.typeform.com/to/KvYEZp#ext=pause");

