import Mustache from 'mustache';
import rollbarLoader from '../lib/rollbar/rollbar_loader.js';
const rollbar = rollbarLoader();
window.rollbar = rollbar;

export function callbackErrorHandler(originalCallback) {
    // wrapper function for wrapping chrome API callbacks
    // workaround for chrome not sending errors to onerror listner for callbacks
    return function(...parameters){
        try {
            return originalCallback(...parameters);
        } catch (e) {
            rollbar.error(e);
            throw e;
        }
    };
}

export function checkNested(obj, level,  ...rest) {
    // checks if a nested argument exists in an object
    // based on this SO answer:
    // https://stackoverflow.com/a/2631198/855475
    if (obj === undefined) {
        return false;
    }

    if (rest.length == 0 && obj.hasOwnProperty(level)) {
        return true;
    }

    return checkNested(obj[level], ...rest);
}

export function includeReview() {
    const reviewTemplateId = process.env.MICROSOFT ? 'review-no-href-tmpl' : 'review-tmpl';
    const reviewTemplate = document.getElementById(reviewTemplateId).innerHTML;
    Mustache.parse(reviewTemplate);

    document.getElementById('review-container').innerHTML = Mustache.render(reviewTemplate);
}
